import React, { useReducer } from 'react';
import Page from 'components/Page';
import WithFooter from 'components/WithFooter';
import EditCategoriesPage from 'components/EditCategoriesPage';
import Button from 'components/Button';
import Footer from 'components/Footer';

const defaultCategories = [
  {
    id: 'food',
    name: 'Food',
    selected: false
  },
  {
    id: 'fashion',
    name: 'Fashion',
    selected: false
  },
  {
    id: 'fitness',
    name: 'Fitness',
    selected: true
  },
  {
    id: 'beauty',
    name: 'Beauty',
    selected: false
  },
  {
    id: 'home_decor',
    name: 'Home Decor',
    selected: false
  }
];

const categoriesReducer = (categories, action) => {
  if (action.type === 'toggle') {
    return [
      ...categories.slice(0, action.i),
      {
        ...categories[action.i],
        selected: !categories[action.i].selected
      },
      ...categories.slice(action.i + 1, categories.length)
    ];
  }
  return categories;
};

const EditCategoriesForm = ({ history }) => {
  const initialCategories =
    JSON.parse(localStorage.getItem('categories')) || defaultCategories;

  const [categories, dispatch] = useReducer(
    categoriesReducer,
    initialCategories
  );

  const submit = () => {
    window.localStorage.setItem('categories', JSON.stringify(categories));
    history.goBack();
  };

  return (
    <Page
      className="lowLevelPage"
      title="Edit Categories"
      onBackNavigation={() => history.goBack()}
    >
      <WithFooter
        footer={
          <Footer>
            <Button onClick={submit}>Update</Button>
          </Footer>
        }
      >
        <EditCategoriesPage
          categories={categories}
          onToggleCategory={i => dispatch({ type: 'toggle', i })}
        />
      </WithFooter>
    </Page>
  );
};

export default EditCategoriesForm;
