import React from 'react';
import cn from 'classnames';
import ToggleButton from 'components/ToggleButton';
import styles from './EditCategoriesPage.module.css';

const EditCategoriesPage = ({
  className,
  categories,
  onToggleCategory,
  ...props
}) => {
  return (
    <div className={cn(className)} {...props}>
      <div className={styles.disclaimer}>
        Modify the Categories you would like to be associated with.
      </div>

      <ul className={styles.list}>
        {categories.map((category, i) => (
          <li className={styles.category} key={category.id}>
            {category.name}
            <ToggleButton
              className={styles.button}
              checked={category.selected}
              onClick={() => onToggleCategory(i)}
            />
          </li>
        ))}
      </ul>
    </div>
  );
};

export default EditCategoriesPage;
