import React, { useEffect, useState } from 'react';
import native from 'utils/mediator';
import Page from 'components/Page';
import List, { ListItem } from 'components/List';
import AppVersion from 'components/AppVersion';
import WithFooter from 'components/WithFooter';
import Footer from 'components/Footer';
import Button from 'components/Button';

const CreatorAccount = ({ history }) => {
  const [handle, setHandle] = useState(
    localStorage.getItem('form-value-handle') || 'spotravnyy'
  );
  useEffect(() => {
    return native.on('usernameUpdate', value => {
      console.log(value);
      localStorage.setItem('form-value-handle', value);
      setHandle(value);
    });
  });

  const email =
    localStorage.getItem('form-value-email') || 'spotravnyy@lohika.com';

  return (
    <Page
      className="topLevelPage"
      title="Account Settings"
      onBackNavigation={() => native.navigateBack()}
    >
      <WithFooter footer={<AppVersion />}>
        <List>
          <ListItem
            title="Brand name"
            onClick={() => history.push('/creator/handle')}
          >
            {handle}
          </ListItem>

          <ListItem
            title="Email"
            onClick={() => history.push('/creator/email')}
          >
            {email}
          </ListItem>

          <ListItem onClick={() => history.push('/creator/password')}>
            Update Password
          </ListItem>

          <ListItem onClick={() => history.push('/creator/categories')}>
            Edit categories
          </ListItem>

          <ListItem onClick={() => native.navigateLinkedAccounts()}>
            Linked Accounts
          </ListItem>

          <ListItem onClick={() => history.push('/faq')}>FAQs</ListItem>

          <ListItem onClick={() => native.openEmail()}>Email Feedback</ListItem>

          <ListItem onClick={() => history.push('/privacy_policy')}>
            Privacy Policy
          </ListItem>

          <ListItem onClick={() => history.push('/terms_of_use')}>
            Terms of Use
          </ListItem>
        </List>

        <Footer>
          <Button secondary onClick={() => native.signOut()}>
            Sign Out
          </Button>
        </Footer>
      </WithFooter>
    </Page>
  );
};

export default CreatorAccount;
