import React from 'react';
import {
  HashRouter as Router,
  Route,
  Switch,
  Redirect
} from 'react-router-dom';
import PageTransition from 'react-router-page-transition';
import CreatorAccount from 'containers/CreatorAccount';
import ConsumerAccount from 'containers/ConsumerAccount';
import EditFieldForm from 'containers/EditFieldForm';
import EditCategoriesForm from 'containers/EditCategoriesForm';
import ChangePasswordForm from 'containers/ChangePasswordForm';
import IframePage from './IframePage';
import FaqPage from './FaqPage';

const App = () => {
  return (
    <Router>
      <Route
        render={({ location }) => (
          <PageTransition timeout={500}>
            <Switch location={location}>
              <Route
                exact
                path="/"
                component={() => (
                  <div>
                    <Redirect to="/creator" />
                  </div>
                )}
              />
              <Route exact path="/creator" component={CreatorAccount} />
              <Route
                path="/creator/handle"
                component={props => (
                  <EditFieldForm
                    name="handle"
                    pageTitle="Edit Handle"
                    label="Brand Name"
                    initialValue="spotravnyy"
                    {...props}
                  />
                )}
              />
              <Route
                path="/creator/email"
                component={props => (
                  <EditFieldForm
                    name="email"
                    type="email"
                    pageTitle="Edit email"
                    label="Email"
                    initialValue="spotravnyy@lohika.com"
                    {...props}
                  />
                )}
              />
              <Route
                path="/creator/categories"
                component={props => <EditCategoriesForm {...props} />}
              />
              <Route
                path="/creator/password"
                component={props => <ChangePasswordForm {...props} />}
              />

              <Route exact path="/consumer" component={ConsumerAccount} />
              <Route
                path="/consumer/handle"
                component={props => (
                  <EditFieldForm
                    name="handle"
                    pageTitle="Edit Handle"
                    label="Username"
                    initialValue="spotravnyy"
                    {...props}
                  />
                )}
              />
              <Route
                path="/consumer/email"
                component={props => (
                  <EditFieldForm
                    name="email"
                    type="email"
                    pageTitle="Edit email"
                    label="Email"
                    initialValue="spotravnyy@lohika.com"
                    {...props}
                  />
                )}
              />
              <Route
                path="/consumer/password"
                component={props => <ChangePasswordForm {...props} />}
              />

              <Route
                path="/consumer/faq"
                component={props => <FaqPage {...props} />}
              />
              <Route
                path="/creator/faq"
                component={props => <FaqPage creator {...props} />}
              />

              <Route
                path="/privacy_policy"
                component={props => (
                  <IframePage
                    pageTitle="Privacy Policy"
                    url="https://www.insponetwork.com/pages/privacy_policy/index.html?mobile"
                    {...props}
                  />
                )}
              />

              <Route
                path="/terms_of_use"
                component={props => (
                  <IframePage
                    pageTitle="Terms of Use"
                    url="https://www.insponetwork.com/pages/terms_and_conditions/index.html?mobile"
                    {...props}
                  />
                )}
              />
            </Switch>
          </PageTransition>
        )}
      />
    </Router>
  );
};

export default App;
