import React from 'react';
import native from 'utils/mediator';
import Page from 'components/Page';
import List, { ListItem } from 'components/List';
import AppVersion from 'components/AppVersion';
import WithFooter from 'components/WithFooter';
import Button from 'components/Button';
import Footer from 'components/Footer';

const ConsumerAccount = ({ history }) => {
  const handle = localStorage.getItem('form-value-handle') || 'spotravnyy';
  const email =
    localStorage.getItem('form-value-email') || 'spotravnyy@lohika.com';

  return (
    <Page
      className="topLevelPage"
      title="Account Settings"
      onBackNavigation={() => native.navigateBack()}
    >
      <WithFooter footer={<AppVersion />}>
        <List>
          <ListItem
            title="Username"
            onClick={() => history.push('/consumer/handle')}
          >
            {handle}
          </ListItem>

          <ListItem
            title="Email"
            onClick={() => history.push('/consumer/email')}
          >
            {email}
          </ListItem>

          <ListItem onClick={() => history.push('/consumer/password')}>
            Update Password
          </ListItem>

          <ListItem onClick={() => history.push('/faq')}>FAQs</ListItem>

          <ListItem onClick={() => native.openEmail()}>Email Feedback</ListItem>

          <ListItem onClick={() => history.push('/privacy_policy')}>
            Privacy Policy
          </ListItem>

          <ListItem onClick={() => history.push('/terms_of_use')}>
            Terms of Use
          </ListItem>
        </List>

        <Footer>
          <Button secondary onClick={() => native.signOut()}>
            Sign Out
          </Button>
        </Footer>
      </WithFooter>
    </Page>
  );
};

export default ConsumerAccount;
