import React from 'react';
import cn from 'classnames';
import styles from './ToggleButton.module.css';

const ToggleButton = ({
  className,
  checked = false,
  checkedLabel = 'Selected',
  uncheckedLabel = 'Select',
  ...props
}) => (
  <button
    type="button"
    className={cn(styles.button, { [styles.checked]: checked }, className)}
    {...props}
  >
    <span className={styles.checkedLabel}>{checkedLabel}</span>
    <span className={styles.uncheckedLabel}>{uncheckedLabel}</span>
  </button>
);

export default ToggleButton;
